import React from 'react';
import HeaderTypeOne from './header-type-one';
import AdvertPage from './advert'
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Redirect
} from "react-router-dom";









class AppTwo extends React.Component {
    render() {
        return (
            <div style={{ fontFamily: "Titillium Web" }}>
                <HeaderTypeOne></HeaderTypeOne>
                <br></br>
                <Router>
                    <Switch>
                        <Route path="/ad/:id" component={AdvertPage} />
                        <Route path="/home">
                            <h1>EVERYTHING SEEMS WORKING</h1>
                        </Route>
                        <Route path="/">
                            <Redirect to={'/ad/swadeshiMarket'} />
                        </Route>
                    </Switch>
                </Router>
                <br></br>
                <div className="bg-dark" style={{ textAlign: 'center', padding: '20px' }}>
                    <h4 style={{ color: 'white' }}>&copy; स्वदेशी Market</h4>
                </div>
            </div>
        );
    }
}

export default AppTwo;