import React from 'react';
import { Image, Card } from 'react-bootstrap';
import GlobalTypes from '../types'






class AdvertPage extends React.Component {
    componentDidMount = async () => {
        let id = this.props.match.params.id;
        console.log(id);
        let response = await fetch(GlobalTypes.API_ENDPOINT + '/api/ad/' + id)
        let data = await response.json();
        this.state = {};
        if (data.id) {
            this.setState({ ...this.state, ...data });
        }
    }
    mediaComp = (media) => {
        if (media?.type === 'emd') {
            return <div className="embed-responsive embed-responsive-16by9 align-middle">
                <iframe className="embed-responsive-item" src={media?.url} allowFullScreen></iframe>
            </div>
        } else {
            return < Image src={media?.url} fluid />
        }
    }
    descriptionCard = (desc) => {
        return <Card style={{ marginBottom: '10px' }}>
            <Card.Body>
                <Card.Title>{desc?.title}</Card.Title>
                <Card.Text>
                    {desc?.value}
                </Card.Text>
            </Card.Body>
        </Card>
    }

    priceCard = (icon, val) => {
        return <Card style={{ textAlign: 'center', marginBottom: '10px' }} >
            <Card.Body>
                <Card.Title><i className={'fa ' + icon + ' mt-4 fa-2x'}></i></Card.Title>
                <Card.Text>
                    {val}
                </Card.Text>
            </Card.Body>
        </Card >
    }

    descriptionsCards = () => {
        return <div className="container-fluid" >
            <div className="row">
                {this.state?.descriptions?.map((des, index) => {
                    return <div key={index} className="col-sm">
                        {this.descriptionCard(des)}
                    </div>
                })}
            </div>
        </div>
    }


    moreImages = () => {
        return <div className="container-fluid">
            <div className="row">
                <div className="col-sm" style={{ marginBottom: '10px' }}>
                    {this.mediaComp(this.state?.medias[1])}
                </div>
                <div className="col-sm" style={{ marginBottom: '10px' }}>
                    {this.mediaComp(this.state?.medias[2])}
                </div>
            </div>
            <div className="row">
                <div className="col-sm" style={{ marginBottom: '10px' }}>
                    {this.mediaComp(this.state?.medias[3])}
                </div>
                <div className="col-sm" style={{ marginBottom: '10px' }}>
                    {this.mediaComp(this.state?.medias[4])}
                </div>
            </div>
        </div>
    }
    render() {
        return (
            <div className="container-fluid" >
                {!this.state?.id ? <div className="alert alert-danger" role="alert">
                    ADVERT NOT FOUND.
                </div> : ''}

                <h1>{this.state?.title}</h1>
                <br></br>
                <div className="row">
                    <div className="col-sm" style={{ marginBottom: '10px' }}>
                        {this.mediaComp(this.state?.medias[0])}
                    </div>
                    <div className="col-sm">
                        {this.moreImages()}
                    </div>
                </div>

                <br></br>
                {this.descriptionsCards()}
                <br></br >

                <div className="row">
                    <div className="col-sm">
                        {this.priceCard('fa-rupee', this.state?.price.value + ' ' + this.state?.price.description)}
                    </div>
                </div>
                <br></br>

                <div className="row">
                    <div className="col-sm">
                        {this.priceCard('fa-phone', this.state?.contactNumber)}
                    </div>
                    <div className="col-sm">
                        {this.priceCard('fa-envelope', this.state?.email)}
                    </div>
                    <div className="col-sm">
                        {this.priceCard('fa-map-marker', this.state?.address)}
                    </div>
                </div>
            </div >
        );
    }
}


export default AdvertPage;